.hero {
  background-image: url("./../img/hero/boxes-2x.webp"),
    url("./../img/hero/back-2x.webp");
  background-size: 20rem 43rem, cover;
  background-repeat: no-repeat;
  background-position: left bottom, top;

  padding-top: 3rem;
  padding-bottom: 22rem;

  position: relative;

  @include mobile {
    padding-bottom: 65rem;
    background-image: url("./../img/hero/back-2x.webp");
    background-size: cover;
  }

  &__top {
    margin-bottom: 10rem;

    @include tablet {
      margin-bottom: 5rem;
    }

    @include small-tablet {
      margin-bottom: 4.7rem;
    }
  }

  &__content {
    max-width: 72rem;
    position: relative;
    z-index: 4;

    @include tablet {
      max-width: 75rem;
      margin: 0 auto;
    }
  }

  &__image {
    position: absolute;
    bottom: 0;
    right: 25rem;
    z-index: 1;
    height: 73rem;
    width: auto;

    @include tablet {
      height: 43rem;
      right: -4rem;
    }

    @include mobile {
      height: auto;
      left: var(--container-offset);
      right: var(--container-offset);
      bottom: 20rem;
    }

    img {
      height: 100%;
      width: auto;

      @include mobile {
        width: 100%;
        height: auto;
      }
    }
  }

  &__box {
    position: absolute;
    z-index: 2;
    right: 0;
    bottom: 0;
    width: 44rem;
    height: 26.8rem;

    @include tablet {
      width: 26.8rem;
    }

    @include mobile {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: bottom right;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 2.3rem;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 3.8rem;
    line-height: 130%;
    color: #2d3241;

    @include tablet {
      font-size: 3rem;
      line-height: 120%;
      margin-bottom: 2rem;
    }

    @include small-tablet {
      line-height: 130%;
      font-size: 24px;
    }

    &--bold {
      font-weight: 700;
    }

    &--red {
      color: #ce3c46;
    }
  }

  &__btn {
    height: 11rem;

    &:nth-child(1) {
      flex-grow: 1;
    }

    @include tablet {
      padding: 2.4rem 4rem;
      max-width: 35rem;
    }

    @include small-tablet {
      height: 90px;
      padding: 28px 50px 17px;
      width: 100%;
      max-width: 41rem;
    }

    @include mobile {
      max-width: 100%;
    }
  }

  &__desc {
    margin: 0;
    margin-bottom: 5.5rem;
    display: flex;
    align-items: flex-start;
    gap: 2.2rem;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 2.3rem;
    line-height: 140%;
    color: #606778;

    @include tablet {
      font-size: 2rem;
      margin-bottom: 4rem;
      max-width: 80%;
    }

    @include small-tablet {
      font-size: 18px;
      line-height: 140%;
      max-width: 100%;
      gap: 1.5rem;
    }

    &::before {
      content: "";
      position: relative;
      flex-shrink: 0;
      display: block;
      width: 2.5rem;
      margin-top: 1.3rem;
      height: 0.1rem;
      background-color: currentColor;
    }
  }

  &__buttons {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 2rem;

    @include tablet {
      justify-content: flex-start;
    }

    @include small-tablet {
      flex-direction: column;
      gap: 3rem;
    }
  }

  &__extra {
    position: relative;
    z-index: 2;
    margin-top: -12rem;
    margin-bottom: 10rem;

    @include tablet {
      margin-top: -8rem;
      margin-bottom: 6rem;
    }

    @include mobile {
      margin-top: -21rem;
      margin-bottom: 60px;
    }
  }

  &__wrapper {
    position: relative;
  }
}

.hero__bottom {
  padding: 4rem 3.6rem;
  background-color: #fff;
  box-shadow: 0 4px 20px 0 rgba(199, 219, 239, 0.5);
  border-radius: 1rem;

  display: flex;
  gap: 9rem;
  position: relative;

  @include tablet {
    padding: 2rem 3rem;
    margin: 0 var(--container-offset);
  }

  @include small-tablet {
    flex-direction: column;
    gap: 3.5rem;
  }

  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    left: 1.7rem;
    right: 1.7rem;
    bottom: -2rem;
    border-radius: 1rem;
    height: 100%;
    background-color: #fff;
    box-shadow: 0 4px 20px 0 rgba(199, 219, 239, 0.5);

    @include small-tablet {
      left: 0.4rem;
      right: 0.4rem;
      bottom: -1.7rem;
    }
  }
}

.hero-item {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 3.6rem;

  @include small-tablet {
    flex-direction: column;
    align-items: flex-start;
    gap: 2rem;
  }

  &__image {
    width: 12.3rem;
    height: 12.3rem;
    border-radius: 0.5rem;
    flex-shrink: 0;
    background-color: #e0eaf6;
    padding: 0 0.9rem;
    padding-top: 1rem;

    position: relative;

    @include small-tablet {
      width: 9rem;
      height: 9rem;
    }

    &--fill {
      padding: 0;
    }

    img {
      width: 100%;
      height: 100%;
    }

    &::before {
      content: "";
      position: absolute;
      z-index: 2;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 4.5rem;
      background: linear-gradient(0deg,
          #f1f5fa 0%,
          rgba(241, 245, 250, 0) 100%);
    }

    &::after {
      content: "";
      position: absolute;
      z-index: 4;
      right: -0.8rem;
      bottom: -0.6rem;
      width: 4rem;
      height: 4rem;
      background-image: url("./../img/check.svg");
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;

      @include small-tablet {
        width: 3rem;
        height: 3rem;
      }
    }
  }

  &__content {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1.8rem;
    line-height: 140%;
    color: #2d3241;

    @include tablet {
      font-size: 1.6rem;
      line-height: 130%;
    }

    @include small-tablet {
      font-size: 16px;
    }

    span {
      display: block;
      font-weight: 700;
    }
  }
}



.hero-time {
  position: absolute;
  z-index: 3;
  right: 0;
  top: -3rem;
  width: 12.5rem;

  @include tablet {
    width: 9.2rem;
    right: 7rem;
    top: 25rem;
  }

  @include small-tablet {
    width: 92px;
  }

  @include mobile {
    top: unset;
    bottom: -23rem;
    right: 15px;
  }

  &__icon {
    position: absolute;
    z-index: 2;
    left: 50%;
    transform: translate(-50%, -50%);
    top: 0;
    width: 8.2rem;
    aspect-ratio: 1 / 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2.5rem;
    mask-image: url('./../img/yellow-mask.svg');
    mask-position: bottom;
    mask-size: contain;
    mask-repeat: no-repeat;
    background-image: linear-gradient(to bottom, #FFE18F, #FBCD5D, #F6C24C);

    @include tablet {
      width: 6.5rem;
      padding: 1.6rem;
    }

    @include small-tablet {
      width: 65px;
      padding: 12px;
    }

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__body {
    backdrop-filter: blur(24px);
    background: rgba(219, 230, 241, 0.3);
    border-radius: 1rem;
    padding: 2.8rem;
    padding-top: 4.2rem;

    @include tablet {
      padding: 1.4rem;
      padding-top: 4.2rem;
    }

    @include small-tablet {
      padding: 14px;
      padding-top: 42px;
    }
  }

  &__value {
    margin-bottom: 0.8rem;
    font-weight: 700;
    font-size: 2.2rem;
    line-height: 100%;
    color: #fff;

    @include tablet {
      font-size: 1.8rem;
    }

    @include small-tablet {
      font-size: 18px;
      margin-bottom: 8px;
    }
  }

  &__desc {
    font-weight: 400;
    font-size: 1.8rem;
    line-height: 120%;
    color: #fff;

    @include tablet {
      font-size: 1.6rem;
    }

    @include small-tablet {
      font-size: 16px;
    }
  }
}